<template>
    <!--地址编辑页内容-->
    <div class="_address d-flex justify-content-center align-items-start ">
        <div id="_address-box" class="_address-box card card-body px-0 mx-0 ">
            <!--地址信息管理栏-->
            <div class="d-flex justify-content-between pb-2 pb-sm-3">
                <span class="txt-body txt-subtitle-space  px-2 px-sm-3 "
                      style="font-weight:bold" @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px color:#1f1f1f"></i>
                    编辑收货地址</span>
                <div id="_delete" class="txt-subbody  px-4 " style="color:red" @click="dialogVisible = true" v-if="type==='edit'">
                    删除
                </div>
            </div>
            <!--地址信息栏-->
            <div class="_addressContent">
                <div class="_shopItemBox bg-white py-2 row d-flex flex-column align-items-center justify-content-start">
                    <div class="row py-2 px-0">
                        <div class="col-3 text-left">
                            收货人
                        </div>
                        <div class="col-9 text-left">
                            <input type="text" class="w-100 p-2  rounded rounded-lg" v-model="addressInfo.name"
                                   minlength="2"  title="请输入正确的收货人姓名" required/>
                        </div>
                    </div>
                    <div class="row py-2 px-0">
                        <div class="col-3 text-left">
                            手机号码
                        </div>
                        <div class="col-9  text-left">
                            <input type="text" class="w-100 p-2  rounded rounded-lg" v-model="addressInfo.tel"
                                   title="手机号码为11位" minlength="11" maxlength="11" required @change="noStrenthen"/>
                        </div>
                    </div>
                    <div class="row py-2 px-0">
                        <div class="col-3 text-left">
                            所在地区
                        </div>
                        <div class="col-9">
                            <div class="w-100   d-flex justify-content-between txt-subbody ">
                                <select name="provLevel" id="provLevel" class="text-truncate" @change="setCity"
                                        style="width:33%;" v-model="addressInfo.province"  >
                                    <option>请选择省份</option>
                                </select>
                                <select name="cityLevel" id="cityLevel" class="text-truncate" @change="setCounty"
                                        style="width:33%;" v-model="addressInfo.city"  >
                                    <option>请选择城市</option>
                                </select>
                                <select name="areaLevel" id="areaLevel" class="text-truncate" @change="getCounty"
                                        style="width:34%;" v-model="addressInfo.county"  >
                                    <option>请选择域市</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="row py-2 px-0">
                        <div class="col-3 text-left">
                            详细地址
                        </div>
                        <div class="col-9 text-left">
                            <textarea name="" id="" cols="30" rows="10"  class="detailMessage w-100 py-1 px-2 rounded rounded-lg"
                                      style="max-height:4em;overflow:hidden"
                                      v-model="addressInfo.addressDetail" required></textarea>
                        </div>
                    </div>
                    <div class="row py-2 px-0">
                        <div class="col-6 text-left px-3 d-flex justify-content-center">
                            <span>设为默认收货地址</span>
                        </div>
                        <div class="col-6 text-right d-flex justify-content-center ">
                            <el-switch
                                    v-model="addressInfo.isDefault"
                                    active-color="red"
                                    inactive-color="#748181">
                            </el-switch>
                        </div>
                    </div>
                    <div class="row d-flex justify-content-center align-items-center py-5">
                        <div class="w-75 rounded rounded-pill text-white py-2" style="background:#ECA31C"
                             @click="onSave">
                            保存
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--地址信息删除栏-->
    <el-dialog
            title="提示"
            :modelValue="dialogVisible"
            width="30%"
            :before-close="handleClose">
        <span class="txt-message">请确认是否删除当前的地址</span>
        <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onDeletePre(true)">确 定</el-button>
                <el-button @click="onDeletePre(false)">取 消</el-button>
            </span>
    </el-dialog>
</template>
<script>
    import {tdist} from 'utils/addressMessage';
    import {ElMessage,ElLoading} from 'element-plus';
    import {addAddress, editAddress, deleteAddress, getAddressDetail} from "network/address";

    import {reactive, onMounted, toRefs, computed, ref} from 'vue';
    import {useRoute, useRouter} from 'vue-router'
    import {useStore} from 'vuex'

    export default {
        name: "AddressEdit",
        setup() {
            const route = useRoute();
            const router = useRouter();
            const store = useStore();

            const state = reactive({
                areaList: {                          //已经有省，县级数据
                    province_list: {},
                    city_list: {},
                    county_list: {}
                },
                addressInfo: {},                    // 地址全部数据（实时数据变换）
                addressInfoPre:{},                  // 用户没有修改之前的数据

                isDefault: true,
                isChange: false,

                totalAddress: [                      // 这一步可能通过数据来优化。并不需要每次都进行遍历获取
                    /*{
                        name:'',            // 省级
                        city:[
                            {
                                name:'',   // 市级
                                area:[]    // 县级
                            }
                        ]
                    }*/
                ],

                //  首先提供用户  全部省份                      1. 先提供省份
                // 然后用户选择一个省份，进行选择市级
                // 然后用户选择一个市级，进行选择县级


                type: 'add',                             // 添加或删除
                addressId: '',                           // 地址id
            });
            let dialogVisible = ref(false);              // 删除弹框

            // 编译后自动执行
            onMounted(() => {
                // 接收参数
                const {type, addressId} = route.query;
                state.type = type;
                state.addressId = addressId;

                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '._address-box'});

                getAllAddress();              // 获取所有的地址数据

                if (state.type === 'edit') {
                    getAddressDetail(addressId).then(res => {     // 获取修改信息
                        const addressDetail = res;
                        console.log('addressedit-data:', res);
                        let _areaCode = '';

                        // 区域码，即id
                        // _areaCode = '140101';
                        let c = state.areaList.county_list;
                        // 遍历对象数据，c_item可遍历出对象的方法和属性名称，c[c_item]即为对应的属性值和方法
                        for (var c_item in c) {
                            if (typeof (c[c_item]) != 'function') {
                                if (c[c_item] == addressDetail.county) {
                                    // console.log('c_item:',c_item);
                                    _areaCode = c_item;
                                }
                            }
                        }
                        // 重新载入修改信息
                        state.addressInfoPre = {
                            name: addressDetail.name,
                            tel: addressDetail.phone,
                            province: addressDetail.province,
                            city: addressDetail.city,
                            county: addressDetail.county,
                            addressDetail: addressDetail.address,
                            isDefault: addressDetail.is_default == 1 ? true : false,
                            areaCode: _areaCode,             // 通过省市区获取的数据
                        };
                        state.addressInfo = {
                            name: addressDetail.name,
                            tel: addressDetail.phone,
                            province: addressDetail.province,
                            city: addressDetail.city,
                            county: addressDetail.county,
                            addressDetail: addressDetail.address,
                            isDefault: addressDetail.is_default == 1 ? true : false,
                            areaCode: _areaCode,             // 通过省市区获取的数据
                        };
                        console.log("_areaCode:", _areaCode);
                        console.log('addressinfo:', state.addressInfo);
                        console.log('数据是',state.addressInfo.province,state.addressInfo.city,state.addressInfo.county);
                        setProvince(addressDetail.province);
                        setCity(addressDetail.city);
                        setCounty(addressDetail.county);
                    }).catch(err=>{
                        ElMessage.error('获取地址信息失败，请刷新重试！')
                    });
                }else if(state.type==="add"){

                    initProvince();
                    state.addressInfoPre = {
                        name: '',
                        tel: '',
                        province: '',
                        city: '',
                        county: '',
                        addressDetail: '',
                        isDefault: false,
                    };
                    state.addressInfo  = {
                        name: '',
                        tel: '',
                        province: '',
                        city: '',
                        county: '',
                        addressDetail: '',
                        isDefault: false,
                    };
                }
                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            });

            // 获取所有的地址数据
            const getAllAddress = () => {
                // 省市区 列表 构造出来
                let _province_list = {};
                let _city_list = {};
                let _county_list = {};


                tdist.getLev1().forEach(p => {
                    // _province_list[p.id] = p.text;

                    var city = [];
                    tdist.getLev2(p.id).forEach(c => {
                        //_city_list[c.id] = c.text;
                        var area = [];
                        tdist.getLev3(c.id).forEach(q => {
                            //_county_list[q.id] = q.text;
                            area.push(q.text);
                            //console.log("---",p.id,p.text,"---",c.id,c.text,"---",q.id,q.text)
                        });
                        city.push({name: c.text, area: area})

                    });
                    state.totalAddress.push({name: p.text, city: city})
                });
                console.log(state.totalAddress);
                state.areaList.province_list = _province_list;
                state.areaList.city_list = _city_list;
                state.areaList.county_list = _county_list;
                setProvince();
            };
            // 初始化设置
            const initProvince = ()=>{              // 目前来说 没有效果
                setProvince();
                $('#provLevel')[0].selectedIndex = 0;
                setCity();
                $('#cityLevel')[0].selectedIndex = 0;        // 下拉列表一般不使用i来作为下标值
                setCounty();
                $('#areaLevel')[0].selectedIndex = 0;
            };

            // 设置第一个省份栏数据
            const setProvince = (province=null) => {
                var nodeProvince = $('#provLevel')[0];        // 下拉列表一般不使用i来作为下标值
                for (let i = 0; i < state.totalAddress.length; i++) {
                    nodeProvince.options[nodeProvince.length] = new Option(state.totalAddress[i].name);
                    if(province !== null && state.totalAddress[i].name===province){
                        nodeProvince.selectedIndex = i+1;
                        province = null;           //避免多次匹配
                    }
                }
            };

            // 省份触发市级栏数据
            const setCity = (city=null) => {
                var nodeProvince = $('#provLevel')[0];
                var nodeCity = $('#cityLevel')[0];        // 下拉列表一般不使用i来作为下标值
                var nodeCounty = $('#areaLevel')[0];


                if (nodeProvince.selectedIndex !== 0) {
                    nodeCity.length = 0;
                    nodeCity.options[nodeCity.length] = new Option("请选择本省城市");

                    for (let i = 0; i < state.totalAddress[nodeProvince.selectedIndex - 1].city.length; i++) {
                        // 数组的遍历，将城市的数据添加到列表项中
                        nodeCity.options[nodeCity.length] = new Option(state.totalAddress[nodeProvince.selectedIndex - 1].city[i].name);

                        if(city !== null && state.totalAddress[nodeProvince.selectedIndex - 1].city[i].name=== city){
                            nodeCity.selectedIndex = i+1;
                            city = null;           //避免多次匹配
                        }
                    }
                } else {
                    //如果没有选择数据的，将会重新设置下拉列表
                    nodeCity.length = 0;
                    nodeCity.options[nodeCity.length] = new Option("请选择省份");
                    nodeCounty.length = 0;
                    nodeCounty.options[nodeCounty.length] = new Option("请选择城市");
                }

            };

            // 市级触发县级栏数据
            const setCounty = (county=null) => {
                var nodeProvince = $('#provLevel')[0];
                var nodeCity = $('#cityLevel')[0];        // 下拉列表一般不使用i来作为下标值
                var nodeCounty = $('#areaLevel')[0];

                if (nodeCity.selectedIndex !== 0) {
                    nodeCounty.length = 0;
                    nodeCounty.options[nodeCounty.length] = new Option("诸选择本市区/县");
                    for (let i = 0; i < state.totalAddress[nodeProvince.selectedIndex - 1].city[nodeCity.selectedIndex - 1].area.length; i++) {
                        nodeCounty.options[nodeCounty.length] = new Option(state.totalAddress[nodeProvince.selectedIndex - 1].city[nodeCity.selectedIndex - 1].area[i]);

                        if(county !== null && state.totalAddress[nodeProvince.selectedIndex - 1].city[nodeCity.selectedIndex - 1].area[i]=== county){
                            nodeCounty.selectedIndex = i+1;
                            county = null;           //避免多次匹配
                        }
                    }
                } else {
                    //如果没有选择数据的，将会重新设置下拉列表
                    nodeCounty.length = 0;
                    nodeCounty.options[nodeCounty.length] = new Option("请选挥城市");
                }
            };
            // 获取县级数据,立即变换地址详情获取输入焦点
            const getCounty = () => {
                console.log($('#areaLevel')[0].selectedIndex);
                $('.detailMessage').focus();
            };

            // 验证输入框数据的正确性
            const verifyInputMessage = ()=>{
                   if(state.addressInfo.name.replace(' ','').length < 2){
                       ElMessage.error('收货人名字长度必需大于等于2');
                       return false;
                   }
                   if(state.addressInfo.tel.replace(' ','').length !== 11){
                       ElMessage.error('收货人电话号码长度必需等于11位');
                       return false;
                   }
                   if(state.addressInfo.province.replace(' ','').length === 0 || state.addressInfo.province.startsWith("请")){
                       ElMessage.error('收货地址需要选择省份');
                       return false;
                   }
                   if(state.addressInfo.city.replace(' ','').length === 0 || state.addressInfo.city.startsWith("请")){
                       ElMessage.error('收货地址需要选择城市');
                       return false;
                   }
                   if(state.addressInfo.county.replace(' ','').length  === 0 || state.addressInfo.county.startsWith("请")){
                       ElMessage.error('收货地址需要选择区/县');
                       return false;
                   }
                   if(state.addressInfo.addressDetail.replace(' ','').length <= 2){
                       ElMessage.error('收货地址需要填写详细地址');
                       return false;
                   }
                   if(state.addressInfoPre.name === state.addressInfo.name
                      && state.addressInfoPre.tel === state.addressInfo.tel
                      && state.addressInfoPre.province === state.addressInfo.province
                      && state.addressInfoPre.city === state.addressInfo.city
                      && state.addressInfoPre.county === state.addressInfo.county
                      && state.addressInfoPre.addressDetail === state.addressInfo.addressDetail
                      && state.addressInfoPre.isDefault === state.addressInfo.isDefault){     // 检测数据是否改变
                       state.isChange = false;
                   } else{
                       state.isChange = true;
                   }
                   return true
            };

            const saveStateMessage = ()=>{
                store.commit('setAddressId',0);          // 先设置为0
                store.commit('setAddressUserName',state.addressInfo.name);
                store.commit('setAddressUserPhone',state.addressInfo.tel);
                store.commit('setAddressUserProvince',state.addressInfo.province);
                store.commit('setAddressUserCity',state.addressInfo.city);
                store.commit('setAddressUserCounty',state.addressInfo.county);
                store.commit('setAddressUserDetail',state.addressInfo.addressDetail);
            };

            // 添加或修改地址
            const onSave = () => {
                var res_result = verifyInputMessage();      //验证输入框的数据
                if (res_result){
                    const params = {
                        name: state.addressInfo.name,
                        phone: state.addressInfo.tel,
                        province: state.addressInfo.province,
                        city: state.addressInfo.city,
                        county: state.addressInfo.county,
                        address: state.addressInfo.addressDetail,
                        is_default: state.addressInfo.isDefault ? 1 : 0
                    };


                    if (state.type === 'edit') {     // 如果数据没有改变，就不会提交到数据库
                        //调用接口修改数据
                        if(state.isChange){
                            editAddress(state.addressId, params).then(res=>{
                                if (res.status !== 422){
                                    ElMessage.success('地址修改成功');
                                    if( state.isDefault){
                                        saveStateMessage();
                                    }
                                    setTimeout(() => {
                                        router.back();
                                    }, 1000);
                                }
                            }).catch(err=>{
                                console.log("addressedit-editaddress-err",err);
                                ElMessage.success('地址修改失败')
                            });
                        }else{
                            ElMessage.success('地址修改成功')
                        }
                    } else if (state.type === 'add') {
                        //调用接口添加数据
                        addAddress(params).then(res=>{
                            if (res.status !== 422){
                                ElMessage.success('地址添加成功');
                                if( state.isDefault){
                                    saveStateMessage();
                                }
                                setTimeout(() => {          // 只有数据成功保存，才会跳转页面
                                    router.back();
                                }, 1000);
                            }
                        }).catch(err=>{
                            console.log("addressedit-addaddress-err",err);
                            ElMessage.success('地址添加失败');
                        });
                    }
                }
            };

            // 删除前的预处理
            const onDeletePre = (flag) =>{
                dialogVisible.value = false;
                if(flag){
                    onDelete();
                }
            };

            // 确认是否删除的弹框
            const handleClose = (done) => {
                dialogVisible.value = false;
                done();
            };

            // 删除地址, 只有在编辑状态下才能删除
            const onDelete = () => {
                deleteAddress(state.addressId).then(res => {
                    ElMessage.success('已经成功删除当前地址');
                    if(store.getters.getAddressId === state.addressId)           // 如果删除的是默认数据，则确认把默认值设置为空
                        store.commit('setAddressId','');            // 决定性属性，设置为空就可以
                    setTimeout(() => {
                        router.back();
                    }, 1000)
                }).catch(err => {
                    console.log('addressedit-deleteaddress-err',err);
                    ElMessage.error('删除地址失败')
                })
            };

            // 对输入的电话号码不能超出长度
            const noStrenthen = () =>{
                if(state.addressInfo.tel>11){
                    state.addressInfo.tel =state.addressInfo.tel.slice(0,12);
                }
            };

            return {...toRefs(state), dialogVisible, handleClose, onDeletePre,  onSave, onDelete,
                 setCity, setCounty, getCounty, noStrenthen}
        }
    }
</script>
<style lang="scss">
    .el-dialog__close{
        font-size:25px!important;
    }
    .el-dialog__body{
        display:flex;
        flex-direction: column;
        justify-content:space-around;
        .txt-message{
            font-size:16px;
            text-align:left;
        }
        .dialog-footer{
            padding-top:15px;
            text-align:right;
        }
    }

    ._address {
        min-height: 600px;

        #_address-box {
            position: relative;
            max-width: 1200px;
            min-height: 580px;
            border: 1px solid red;
            background: linear-gradient(to left top, #F1E4EE, #F9F4EF);

            ._shopItemBox {
                border-radius: 10px;
                border: 3px solid #F3E3DE;
                width: 99%;
                margin: auto;
                position: relative;
            }
        }

        input, textarea {
            border: none;
            outline: none;
        }

        input[type=text], textarea {
            background: #F1E4EE;
        }

        textarea {
            resize: none;
        }

        select {
            /*清除select默认样式*/
            appearance: none;
            -moz-appearance: none;
            -webkit-appearance: none;
            -ms-appearance: none;

            border: 1px solid #CCC;
            width: 330px;
            height: 44px;
            /*自定义箭头的样式，记得背景一定要加 白色或其他*/
            /*ie下,原默认的箭头样式还是会显示，所以这里把自定义的样式给去除了*/
            background: #fff;
            color: #666;
            padding: 8px;
            outline: none;
        }
    }

</style>